import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents';
import { Icon } from '../../../commanComponent';
import { setStatusColor } from '../../../../utils/common';

const ManagerTeamTable = (props) => {
    const { currentPage, setCurrentPage, employeeData ,toggleEmpRatingModal} = props

    const ratingDurationField = [
        { key: 'sn', label: 'S.N' },
        { key: 'empId', label: 'Emp Id' },
        { key: 'empName', label: 'Emp Name' },
        { key: 'joiningDate', label: 'Joining Date' },
        { key: 'department', label: 'Department' },
        { key: 'designation', label: 'Designation' },
        { key: 'location', label: 'Location' },
        { key: 'kraAssigned', label: 'KRA Assigned' },
        { key: 'status', label: 'Account Status' },
        { key: 'action', label: 'Action' }
    ];
    const ratingDurationScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        status: (item, index) => (
            <span
                className={`${setStatusColor(item?.status)}`}
            >
                {item?.status}
            </span>
        ),

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li>
                        <span
                            className="dropdown-item"
                          onClick={() => toggleEmpRatingModal(item)}
                        >
                            Start Rating
                        </span>
                    </li>
                </ul>
            </div>
        )
    }), [employeeData]);
    return (
        <div>
            <div className="table-responsive tableDiv text-nowrap">
                <CustomTable
                    data={employeeData || []}
                    fields={ratingDurationField}
                    // isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={employeeData?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={ratingDurationScopedSlots}
                />
            </div>
        </div>
    )
}

export default ManagerTeamTable
