import React, { useMemo } from 'react'
import { CustomTable } from '../../../reuseableComponents'

const RatingWiseEmp = (props) => {
    const { currentPage, setCurrentPage, employeeRatingsData } = props

    const ratingWiseEmpField = [
        { key: 'sn', label: 'S.N' },
        { key: 'empId', label: 'Emp Id' },
        { key: 'empName', label: 'Emp Name' },
        { key: 'joiningDate', label: 'Joining Date' },
        { key: 'department', label: 'Department' },
        { key: 'designation', label: 'Designation' },
        { key: 'yearlyAverageRating', label: 'Yearly Average Rating' },
    ];
    const ratingWiseEmpScopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

    }), [employeeRatingsData]);
  return (
    <div>
       <div className="table-responsive tableDiv">
       <CustomTable
                    data={employeeRatingsData || []}
                    fields={ratingWiseEmpField}
                    // isLoading={isLoading}
                    pagination={true}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    totalPages={employeeRatingsData?.totalPages}
                    isHeader={false}
                    tableStyle={{ padding: "0px", boxShadow: "none" }}
                    scopedSlots={ratingWiseEmpScopedSlots}
                />

       </div>
    </div>
  )
}

export default RatingWiseEmp
